@font-face {
  font-family: 'jsm-icons';
  src: url('../font/jsm-icons.eot?2019533');
  src: url('../font/jsm-icons.eot?2019533#iefix') format('embedded-opentype'),
       url('../font/jsm-icons.woff2?2019533') format('woff2'),
       url('../font/jsm-icons.woff?2019533') format('woff'),
       url('../font/jsm-icons.ttf?2019533') format('truetype'),
       url('../font/jsm-icons.svg?2019533#jsm-icons') format('svg');
  font-weight: normal;
  font-style: normal;
}
/* Chrome hack: SVG is rendered more smooth in Windozze. 100% magic, uncomment if you need it. */
/* Note, that will break hinting! In other OS-es font will be not as sharp as it could be */
